@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
html{
  overflow:hidden;
}


.preloader {
  align-items: center;
  background: rgba(4, 10, 37,.9);
  display: flex;
  height: 100vh;
  justify-content: center;
  left: 0;
  position: fixed;
  top: 0;
  transition: opacity 0.3s linear;
  width: 100%;
  z-index: 9999;
}

#root{
  position:absolute;
  -webkit-width:100%;
  -webkit-height:100%;
  width:100%;
  height:100%;
  display:flex;
  overflow:hidden;
  
}

  .serviceHolder{
    min-width:100%;
    max-width:100%;
    width:100%;
     display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    margin-top:30px;
    -webkit-text-fill-color:#000;
  }

.serviceToatsHolder{
   min-width:100%;
    max-width:100%;
    width:100%;
     display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    margin-top:30px;
    -webkit-text-fill-color:#000;
}

.skillzAnime{
  font-size:1rem;
}

.servicesFlow{
  display:flex;
  flex-direction:column;
   justify-content:center;
    align-items:center;
  flex:1 1;
  overflow-x:auto;
    box-shadow:0 0 5px #444;
}

.toatsBlock{
  height:300px;
  width:280px;
}

.toatsText{
  font-size:1rem;
}

.skillzAnime{
   transform: translate3d(20px, 20px, 0px);
  -webkit-animation:skillz 2s linear infinite;
  animation:skillz 2s linear infinite;
  
}


.phone_{
   transform: translate3d(20px, 20px, 0px);
  -webkit-animation:phone 2s linear infinite;
  animation:phone 2s linear infinite;
  
}





@-webkit-keyframes phone{
   0% { 
         transform:rotateZ(10deg); 

    }
    50% { 
        transform:rotateZ(-40deg); 
    }
    100% { 
         transform:rotateZ(10deg); 
    }
}





@keyframes phone{
   0% { 
         transform:rotateZ(10deg); 

    }
    50% { 
        transform:rotateZ(-40deg); 
    }
    100% { 
         transform:rotateZ(10deg); 
    }
}


@-webkit-keyframes skillz{
   0% {
        transform: translate3d(0px, 40px, 0px);
        -webkit-animation-timing-function: ease-in;
        animation-timing-function: ease-in;
    }
    50% {
        transform: translate3d(0px, 50px, 0px);
        -webkit-animation-timing-function: ease-out;
        animation-timing-function: ease-out;
    }
    100% {
        transform: translate3d(0px, 40px, 0px);
    }
}


@keyframes skillz{
   0% {
        transform: translate3d(0px, 40px, 0px);
        -webkit-animation-timing-function: ease-in;
        animation-timing-function: ease-in;
    }
    50% {
        transform: translate3d(0px, 50px, 0px);
        -webkit-animation-timing-function: ease-out;
        animation-timing-function: ease-out;
    }
    100% {
        transform: translate3d(0px, 40px, 0px);
    }
}

.myRole{
   padding: 40px;
  font-size: 75px;
  font-family: 'Monoton', cursive;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 0 80px red,0 0 30px FireBrick,0 0 6px DarkRed;
  color: red;
}

.myRole p { margin:0; }
.error:hover { text-shadow: 0 0 200px #ffffff,0 0 80px #008000,0 0 6px #0000ff; }
.code:hover { text-shadow: 0 0 100px red,0 0 40px FireBrick,0 0 8px DarkRed; }
.error {
  color: #fff;
  text-shadow: 0 0 80px #ffffff,0 0 30px #008000,0 0 6px #0000ff;
}
.error span {
  -webkit-animation: upper 11s linear infinite;
          animation: upper 11s linear infinite;
}
.code span:nth-of-type(2) {
  -webkit-animation: lower 10s linear infinite;
          animation: lower 10s linear infinite;
}
.code span:nth-of-type(1) {
  text-shadow: none;
  opacity:.4;
}
@-webkit-keyframes upper {
  0%,19.999%,22%,62.999%,64%, 64.999%,70%,100% {
    opacity:.99; text-shadow: 0 0 80px #ffffff,0 0 30px #008000,0 0 6px #0000ff;
  }
  20%,21.999%,63%,63.999%,65%,69.999% {
    opacity:0.4; text-shadow: none; 
  }
}
@keyframes upper {
  0%,19.999%,22%,62.999%,64%, 64.999%,70%,100% {
    opacity:.99; text-shadow: 0 0 80px #ffffff,0 0 30px #008000,0 0 6px #0000ff;
  }
  20%,21.999%,63%,63.999%,65%,69.999% {
    opacity:0.4; text-shadow: none; 
  }
}
@-webkit-keyframes lower {
  0%,12%,18.999%,23%,31.999%,37%,44.999%,46%,49.999%,51%,58.999%,61%,68.999%,71%,85.999%,96%,100% {
    opacity:0.99; text-shadow: 0 0 80px red,0 0 30px FireBrick,0 0 6px DarkRed;
  }
  19%,22.99%,32%,36.999%,45%,45.999%,50%,50.99%,59%,60.999%,69%,70.999%,86%,95.999% { 
    opacity:0.4; text-shadow: none; 
  }
}
@keyframes lower {
  0%,12%,18.999%,23%,31.999%,37%,44.999%,46%,49.999%,51%,58.999%,61%,68.999%,71%,85.999%,96%,100% {
    opacity:0.99; text-shadow: 0 0 80px red,0 0 30px FireBrick,0 0 6px DarkRed;
  }
  19%,22.99%,32%,36.999%,45%,45.999%,50%,50.99%,59%,60.999%,69%,70.999%,86%,95.999% { 
    opacity:0.4; text-shadow: none; 
  }
}


